@import "tailwindcss/base";
@import "tailwindcss/components";
@import "tailwindcss/utilities";

// project specific CSS goes here
html {
  scroll-behavior: smooth;
}

[x-cloak] {
  display: none;
}

body {
  @apply overflow-x-hidden overflow-y-auto w-screen min-h-screen flex flex-col text-gray-700 text-sm font-poppins scrollbar-thin scrollbar-track-gray-700 scrollbar-thumb-[#e37725] relative;
}

#logo-container svg {
  @apply w-32 h-auto
}


